// components/Nexus.tsx
'use client';
import useGaiaState from '@/lib/useGaiaState';
import EarthPreview from './EarthPreview';
import Filament from './Filament';
import React, { useEffect, useRef, useState } from 'react';

export default function Nexus({ result }: { result?: any }){
  const { state, loading, refresh } = useGaiaState();
  const [symbioSummary, setSymbioSummary] = useState<string>('');
  const [symbioDetails, setSymbioDetails] = useState<string>('');
  const [activeCity, setActiveCity] = useState<"Delhi" | "Mumbai" | "Darjeeling">("Delhi");

  // Load symbiosphere observations summary
  useEffect(() => {
    const obs = JSON.parse(localStorage.getItem('gaia_obs') || '[]');
    if (obs.length > 0) {
      const criticalCount = obs.filter((o: any) => o.level === 'critical').length;
      const concernCount = obs.filter((o: any) => o.level === 'concern').length;
      const goodCount = obs.filter((o: any) => o.level === 'excellent' || o.level === 'good').length;
      const categories = [...new Set(obs.map((o: any) => o.type))];
      
      // Build detailed observation list
      const observationDetails = obs.slice().reverse().map((o: any) => {
        return `${o.type} in ${o.loc} (${o.level}): ${o.note}`;
      }).join(' | ');
      
      const summary = `${obs.length} observations: ${goodCount} positive, ${concernCount} concerns, ${criticalCount} critical. Categories: ${categories.join(', ')}.`;
      setSymbioSummary(summary);
      setSymbioDetails(observationDetails);
    }
  }, []);

  const gaiaChaos = !loading && state?.nexus?.chaos != null ? state.nexus.chaos : undefined;
  const gaiaHue = !loading && state?.nexus?.hue != null ? state.nexus.hue : undefined;
  const gaiaRisk = !loading && state?.risk != null ? state.risk : undefined;
  const gaiaTint = !loading && state?.earth_tint ? state.earth_tint : undefined;
  
  const canvasRef = useRef<HTMLCanvasElement|null>(null);
  const [tick, setTick] = useState(0);
  
  // Compute threat vs recovery - use GAIA state data if available
  const gaidLinkedIndex = state?.gaia_link?.biodiversity ?? (result?.index ?? 50);
  const idx = state && !loading ? gaidLinkedIndex : (result?.index ?? 50);
  const threat = Math.max(0, 100 - idx);
  const recovery = idx;

  useEffect(()=>{
    let raf = 0;
    function draw(){
      const canvas = canvasRef.current; if(!canvas) return;
      const ctx = canvas.getContext('2d')!;
      const w = canvas.width, h = canvas.height;
      ctx.clearRect(0,0,w,h);
      const g = ctx.createLinearGradient(0,0,0,h); g.addColorStop(0,'rgba(4,12,20,0.6)'); g.addColorStop(1,'rgba(2,6,10,0.6)');
      ctx.fillStyle = g; ctx.fillRect(0,0,w,h);
      
      const fromResultChaos = result? (idx>65?0.6: idx>40?1.2:2.8) : 1.5;
      const fromResultHue = idx>65?150: idx>40?45:0;
      const chaos = gaiaChaos ?? fromResultChaos;
      const hue = gaiaHue ?? fromResultHue;
      
      // Draw more dramatic filaments based on chaos
      const cols = Math.round(16 + (1/Math.max(1, chaos))*24);
      for(let i=0;i<cols;i++){
        const baseX = (i+0.5)/cols * w;
        let x = baseX + 60*Math.sin((tick + i*20)/60) * chaos;
        let y = 20;
        ctx.beginPath(); ctx.moveTo(x,y);
        const segments = 8;
        for(let s=0;s<segments;s++){
          const amp = Math.max(0.5, (1 - s/segments)) * chaos;
          const cx = x + (Math.random()-0.5)*100*amp + 30*Math.sin((tick + i*10 + s*5)/30);
          const cy = y + (s+1) * (h/(segments+1)) + 15*Math.cos((tick + i*7 + s*11)/35);
          const nx = baseX + (s+1)*(w/(segments+2) - w/(cols*segments)) + Math.sin((tick + s*i)/25)*80*amp;
          ctx.quadraticCurveTo(cx,cy,nx,cy+10);
          x = nx; y = cy+10;
        }
        ctx.lineWidth = 1.0 + 2.0*(1 - idx/100);
        const opacity = 0.4 + (0.8 - chaos/5);
        ctx.strokeStyle = `hsla(${hue},85%,${50 + 25*Math.sin((tick+i)/25)}%,${opacity})`;
        ctx.stroke();
      }
      
      // Enhanced pulsing core
      const cx = w*0.75, cy = h*0.5;
      const pulse = 25 + (result? (result.index/100)*80 : 40) * Math.abs(Math.sin(tick/35));
      const g2 = ctx.createRadialGradient(cx,cy,pulse*0.05,cx,cy,pulse*3.5);
      g2.addColorStop(0,`hsla(${hue},95%,65%,${0.3})`);
      g2.addColorStop(0.5,`hsla(${hue},80%,50%,${0.1})`);
      g2.addColorStop(1,'rgba(10,12,18,0)');
      ctx.fillStyle = g2; ctx.beginPath(); ctx.arc(cx,cy,pulse*3.5,0,Math.PI*2); ctx.fill();
      
      setTick(t => t+1);
      raf = requestAnimationFrame(draw);
    }
    raf = requestAnimationFrame(draw);
    return ()=>cancelAnimationFrame(raf);
  },[result, gaiaChaos, gaiaHue]);

  return (
    <div style={{display:'flex',flexDirection:'column',gap:12}}>
      {/* Top Section: Canvas, Bar, and Right Panel */}
      <div style={{display:'flex',gap:12,alignItems:'flex-start'}}>
        <div style={{flex:1}}>
          <canvas id="nexusCanvas" ref={canvasRef} width={800} height={340} style={{borderRadius:10, display:'block', background:'linear-gradient(180deg, rgba(2,8,14,0.6), rgba(3,12,20,0.6))'}} />
          
          {/* Threat vs Recovery Bar */}
          <div style={{marginTop:12, background:'rgba(255,255,255,0.02)', padding:12, borderRadius:10}}>
            <div style={{display:'flex', justifyContent:'space-between', marginBottom:6}}>
              <div className="muted-block">Threat vs Recovery</div>
              <div className="pill" style={{fontSize:11}}>{threat.toFixed(0)} / {recovery.toFixed(0)}</div>
            </div>
            <div style={{display:'flex', height:24, gap:2, borderRadius:6, overflow:'hidden', background:'rgba(0,0,0,0.2)'}}>
              <div style={{
                flex: threat,
                background: threat > 50 ? 'linear-gradient(90deg, #ff4444, #ff6b6b)' : threat > 25 ? 'linear-gradient(90deg, #ffa500, #ffb84d)' : 'linear-gradient(90deg, #ff9999, #ffcccc)',
                transition: 'all 0.3s ease',
                borderRadius: threat > 0 ? '6px 0 0 6px' : 0,
                display:'flex', alignItems:'center', justifyContent:'center',
                fontSize:10,
                color:'rgba(255,255,255,0.7)',
                fontWeight:600
              }}>
                {threat > 10 && `${threat.toFixed(0)}%`}
              </div>
              <div style={{
                flex: recovery,
                background: recovery > 70 ? 'linear-gradient(90deg, #2de8a7, #20e07a)' : recovery > 40 ? 'linear-gradient(90deg, #56c8ff, #3ad1ff)' : 'linear-gradient(90deg, #ff9f6b, #ffc46a)',
                transition: 'all 0.3s ease',
                borderRadius: recovery > 0 ? '0 6px 6px 0' : 0,
                display:'flex', alignItems:'center', justifyContent:'center',
                fontSize:10,
                color:'rgba(255,255,255,0.9)',
                fontWeight:600
              }}>
                {recovery > 10 && `${recovery.toFixed(0)}%`}
              </div>
            </div>
            <div style={{display:'flex', justifyContent:'space-between', marginTop:4, fontSize:11}}>
              <span className="muted">Stress</span>
              <span className="muted">Stability</span>
            </div>
          </div>
        </div>

        <div style={{width:300}}>
          {/* Earth Preview */}
          <div style={{background:'rgba(255,255,255,0.02)', padding:12, borderRadius:10, textAlign:'center', marginBottom:12}}>
            <div className="muted-block">Earth State</div>
            <div style={{marginTop:8, display:'flex', justifyContent:'center'}}>
              <EarthPreview size={140} />
            </div>
          </div>

          <div style={{background:'rgba(255,255,255,0.02)', padding:10, borderRadius:10}}>
            <div style={{display:'flex',alignItems:'center',gap:10}}>
              <div className="pill" id="nexusMoodTag">
                {state && !loading ? (
                  state.mood || (idx > 65 ? 'Harmonic' : idx > 40 ? 'Tense' : 'Stressed')
                ) : (
                  result ? (result.index > 65 ? 'Harmonic' : result.index > 40 ? 'Tense' : 'Stressed') : '—'
                )}
              </div>
              <div style={{flex:1}}>
                <div className="muted-block">Symbiosis index</div>
                <div id="nexusIndex" style={{fontWeight:700,fontSize:22}}>
                  {state && !loading ? idx.toFixed(0) : (result ? result.index : '—')}
                </div>
              </div>
            </div>
            <div style={{display:'flex', gap:8, marginTop:8, alignItems:'center'}}>
              <div className="muted-block">Risk</div>
              <div className="pill" style={{fontSize:12}}>
                {gaiaRisk ? (gaiaRisk * 100).toFixed(0) + '%' : (result?.risk ?? '—')}
              </div>
              {gaiaTint ? (
                <div style={{marginLeft:'auto', display:'flex', alignItems:'center', gap:8}}>
                  <div className="muted-block">Tint</div>
                  <div style={{width:22,height:22,borderRadius:6,background:gaiaTint,border:'1px solid rgba(255,255,255,0.06)'}} />
                </div>
              ) : null}
            </div>
            <div style={{marginTop:8}}>
              <div className="muted-block">Timeline</div>
              <input id="timeSlider" type="range" min={0} max={100} defaultValue={100} />
              <div style={{display:'flex', gap:8, marginTop:8}}>
                <button className="gaia-btn primary" id="recompute">Recompute Visuals</button>
                <button className="gaia-btn ghost" onClick={() => refresh()} disabled={loading}>{loading ? 'Syncing...' : 'Sync GAIA State'}</button>
                <button className="gaia-btn ghost" id="exportPNG" onClick={()=>{
                  const canvas = canvasRef.current; if(!canvas) return;
                  const a = document.createElement('a'); a.href = canvas.toDataURL('image/png'); a.download = 'gaia_nexus.png'; a.click();
                }}>Export PNG</button>
              </div>
            </div>
          </div>
        </div>
      </div>

      {/* Full Width Interpretation Section */}
      <div style={{background:'rgba(255,255,255,0.02)', padding:12, borderRadius:10}}>
        <div className="muted-block">Interpretation</div>
        <div id="nexusText" style={{marginTop:8, fontSize:13, lineHeight:1.6}}>
          {result || state ? (
            <>
              <strong>Simulation Results:</strong> Climate impact trajectory shows ΔT {state?.gaia_link?.delta_temp ?? result?.tempDrift ?? '—'}°C, biodiversity index at {idx.toFixed(0)}/100, and sea level rise of +{state?.gaia_link?.sea_level ?? result?.seaLevel ?? '—'}m. Ecosystem stress level: {idx > 65 ? 'Low' : idx > 40 ? 'Moderate' : 'High'}.
              <br/>
              <br/>
              <strong>Risk Assessment:</strong> {gaiaRisk ? `Global risk metric: ${(gaiaRisk * 100).toFixed(0)}%.` : 'Calculating planetary risk...'}
              {state?.mood && ` Current GAIA mood: ${state.mood}.`}
              <br/>
              <br/>
              {symbioSummary && (
                <>
                  <strong>Ground Truth (SymbioSphere):</strong> {symbioSummary}
                  {symbioDetails && (
                    <>
                      <br/>
                      <strong>Observations:</strong> {symbioDetails}
                    </>
                  )}
                  <br/>
                  <br/>
                </>
              )}
              <strong>Nexus Filaments:</strong> The animated filament patterns reflect ecosystem connectivity and stress propagation. More chaotic patterns indicate higher environmental turbulence; stable patterns suggest resilience. The color hue (0-360°) encodes systemic balance.
            </>
          ) : (
            'Run a simulation to populate Nexus with a living structure built from the prediction. The filament animation will visualize ecosystem stress and recovery dynamics in real-time.'
          )}
        </div>
      </div>
    </div>
  );
}
